import axios from 'axios'
import iView from 'iview'
// import Vue from 'vue'

// axios
axios.defaults.timeout = 100000 // 
// axios.defaults.baseURL = 'http://localhost:8087' // 
axios.defaults.baseURL = 'https://www.qbian.me/developers'

// 
axios.defaults.transformRequest = function (data) {
  // data = Qs.stringify(data);
  data = JSON.stringify(data)
  return data
}

// axios.defaults.headers['Content-Type'] = 'application/json;charset=UTF-8'
// axios.defaults.headers['token'] = localStorage.getItem('token')

// 
// http request 
axios.interceptors.request.use(
  config => {
    // config.data = JSON.stringify(config.data)
    config.headers['Content-Type'] = 'application/json;charset=UTF-8'
    config.headers.token = localStorage.getItem('token')
    return config
  },
  error => {
    return Promise.reject(error.response)
  }
)

// 
// http response 
axios.interceptors.response.use(
  response => {
    // console.info('response info ===>', response)
    if (response.data.code !== 0) {
      iView.Message.error(response.data.message)
      if (response.data.code === 503) {
        window.location.href = window.location.origin + '/user/login?redirectUrl=' + window.location.href
        // Vue.$router.push('/user/login?redirectUrl=' + window.location.href)
      }
      //  -1 ticket
      // cookie.del("ticket")
      // window.location.href='http://login.com'
    }
    return response.data
  },
  error => {
    return Promise.reject(error.response) // 
  }
)

export default axios



