<style scoped>
</style>
<template>
  <div class="efficient-setting">
    <Row>
      <Col span="4">
        <Menu theme="light" :active-name="tab" @on-select="select">
          <MenuGroup title="">
            <MenuItem name="1">
              <Icon type="ios-at-outline" />
              
            </MenuItem>
            <MenuItem name="2">
              <Icon type="ios-alarm-outline" />
              
            </MenuItem>
          </MenuGroup>
        </Menu>
      </Col>
      <Col span="20" offset="0">
        <Row>
          <Col v-if="tab === '1'" span="16" offset="2">
            <Alert type="warning" show-icon>
              
              <template slot="desc">
                <br/>
                <br/>
                webHookTokenwebHookToken<br/>
                
              </template>
            </Alert>
          </Col>
          <Col v-if="tab === '2'" span="16" offset="2">
            <Alert type="warning" show-icon>
              
              <template slot="desc">
                <br/>
                <br/>
                <br/>
                <br/>
                
              </template>
            </Alert>
          </Col>
        </Row>
        <Row style="margin-top:10px;">
          <Col span="12" offset="2">
            <Form v-if="tab === '1'" ref="newPushAddr" :model="newPushAddr" :rules="newPushAddrValidate" :label-width="120">
              <FormItem label="" prop="type">
                <Select v-model="newPushAddr.type">
                  <Option value="0"></Option>
                  <Option value="1"></Option>
                  <Option value="2"></Option>
                </Select>
              </FormItem>
              <FormItem label="" prop="address">
                <Input v-model="newPushAddr.address" placeholder="..."/>
              </FormItem>
              <FormItem label="" prop="test">
                <Input v-model="newPushAddr.test" type="textarea" :autosize="{minRows: 5,maxRows: 15}" placeholder="..."/>
              </FormItem>
              <FormItem>
                <Button type="primary" @click="savePushAddr"></Button>
              </FormItem>
            </Form>
            <Form v-if="tab === '2'" ref="newPushTime" :model="newPushTime" :rules="newPushTimeValidate" :label-width="120">
              <FormItem label="" prop="projectCategories">
                <Select v-model="newPushTime.projectCategories" placeholder=""  multiple style="width:600px">
                  <Option v-for="item in projectCategorys" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
              </FormItem>
              <FormItem label="" prop="type">
                <Select v-model="newPushTime.type" @on-change="newPushTime.whichDay = ''" style="width:600px;">
                  <Option value="0">24</Option>
                  <Option value="1"></Option>
                </Select>
              </FormItem>
              <FormItem label="" prop="whichDay">
                <Select :multiple="newPushTime.type === '0'" v-model="newPushTime.whichDay" placeholder="" style="width:600px">
                  <Option v-for="item in whichDays" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
              </FormItem>
              <FormItem label="" prop="time">
                <TimePicker v-model="newPushTime.time" type="time" placeholder="" style="width:600px"></TimePicker>
              </FormItem>
              <FormItem>
                <Button type="primary" @click="savePushTime"></Button>
              </FormItem>
            </Form>
          </Col>
        </Row>
      </Col>
    </Row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tab: '1',
      tabObj: {
        value: '1'
      },
      newPushAddr: {
        type: '0',
        address: '',
        test: ''
      },
      newPushAddrValidate: {
        type: [{ required: true, message: '', trigger: 'blur' }],
        address: [{ required: true, message: '', trigger: 'blur' }],
        test: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'string', max: 200, message: '200', trigger: 'blur' }
        ]
      },
      newPushTime: {
        projectCategories: '',
        type: '0', //  || 
        whichDay: '', // 
        time: '' // 
      },
      newPushTimeValidate: {
        projectCategories: [{ required: true, type: 'array', min: 1, message: '', trigger: 'change' }],
        type: [{ required: true, message: '', trigger: 'blur' }],
        // whichDay: [{ required: true, type: 'array', min: 1, message: '', trigger: 'change' }],
        time: [{ required: true, message: '', trigger: 'blur' }]
      },
      projectCategorys: this.$const.constants.projectCategorys,
      whichDays: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }, {
        value: '6',
        label: ''
      }, {
        value: '7',
        label: ''
      }]
    }
  },
  methods: {
    select (name) {
      this.tab = name
      this.$router.push({
        path: '/efficient/setting',
        query: {
          tab: name
        }
      })
      if (this.tab === '1') {
        this.loadAddress()
      }
      if (this.tab === '2') {
        this.loadTime()
      }
    },
    savePushAddr () {
      this.$refs.newPushAddr.validate((valid) => {
        if (!valid) {
          return
        }
        this.$Loading.start()
        this.$http.post(this.$const.efficient.api.push_address_save, this.newPushAddr).then(res => {
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          this.$Loading.finish()
          this.$Message.success('!')
        })
      })
    },
    savePushTime () {
      if (!this.newPushTime.whichDay) {
        this.$Message.error('!')
        return
      }
      this.$refs.newPushTime.validate((valid) => {
        if (!valid) {
          return
        }
        const { projectCategories, type, whichDay, time } = this.newPushTime
        this.$Loading.start()
        this.$http.post(this.$const.efficient.api.push_time_save, {
          projectCategories,
          type,
          whichDay: type === '0' ? '' : whichDay,
          whichDays: type === '1' ? [] : whichDay,
          pushAt: this.$const.util.nowDayTime(time) - this.$const.util.beginDayTime()
        }).then(res => {
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          this.$Loading.finish()
          this.$Message.success('!')
        })
      })
    },
    loadAddress () {
      this.$http.post(this.$const.efficient.api.push_address_get, {}).then(res => {
        if (res.code === 0 && res.data) {
          this.newPushAddr = res.data
          this.newPushAddr.type = res.data.type + ''
        }
      })
    },
    loadTime () {
      this.$http.post(this.$const.efficient.api.push_time_get, {}).then(res => {
        if (res.code === 0 && res.data) {
          this.newPushTime = {
            projectCategories: res.data.projectCategories.map(item => item + ''),
            type: res.data.type + '',
            whichDay: res.data.type === 1 ? res.data.whichDay + '' : res.data.whichDays, // 
            time: this.$const.util.getTimeStr(res.data.pushAt) // 
          }
        }
      })
    }
  },
  created () {
    const tab = this.$router.currentRoute.query.tab + ''
    if (tab === '1') {
      this.loadAddress()
    }
    if (tab === '2') {
      this.loadTime()
    }
  }
}
</script>



